import MzButton from './components/mz-button';/**/
import MzRadio from './components/mz-radio';/**/
import MzBreadcrumb from './components/breadcrumb/mz-breadcrumb'/**/
import MzBreadcrumbItem from './components/breadcrumb/mz-breadcrumb-item'/**/
import MzMenu from './components/menu/mz-menu'/**/
import MzSubmenu from './components/menu/mz-submenu'/**/
import MzMenuItem from './components/menu/mz-menu-item'/**/
var components = [
  MzButton,
  MzRadio,
  MzBreadcrumb,
  MzBreadcrumbItem,
  MzMenu,
  MzSubmenu,
  MzMenuItem
];

const install = function(Vue) {
  components.map(component => {
    Vue.component(component.name, component);
  });
};

/* istanbul ignore if */
if (typeof window !== 'undefined' && window.Vue) {
  install(window.Vue);
};

export default {
  version: '1.0.8',
  install,
  MzButton,
  MzRadio,
  MzBreadcrumb,
  MzBreadcrumbItem,
  MzMenu,
  MzSubmenu,
  MzMenuItem
};



